.title {
    background-color: rgba(15, 15, 90, 0.938);
    color: white;
    padding: 20px;
    margin-top: 10px;
    letter-spacing: 2px;
    font-weight: normal;
}

li {
    text-align: left;
    border: 0;
    padding: 5px;
}

img{
    position: relative;
    width: 100%;
    overflow: hidden;
    /* 이미지의 크기가 크면 나머지 부분 삭제 */
}

th {
    display: table-cell;
    text-align: center;
    vertical-align: middle;
}
.dropdown-model{
    background-color: white;
}